/* Contenedor de la Barra Lateral (aside) */

.grid-aside {
    display: grid;
    grid-template-areas: 'aside';
    grid-gap: 10px;
    background-color: #c27ba0;
    padding: 10px;
}

.grid-aside>div {
    background-color: rgba(202, 199, 199, 0.8);
    text-align: center;
    padding: 20px 0;
    margin: 10px;
}